import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import reportWebVitals from './reportWebVitals';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import { Cargando, Session } from './utils2';

function LoadApp ()
{
  const [loading, setLoading] = useState(true);

  const session = new Session( ses => {
    if (! ses.login && window.location.href.substring(0,16) !== "http://localhost" )
      window.location.assign("/sie-android/Paginas/lgus/?redurl=" + encodeURIComponent(window.location.href) );
    else
      setLoading( false );
  });

  return loading ? <Cargando /> : <App session={session} /> ;

}

ReactDOM.render(
  <React.StrictMode>
    <LoadApp />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

serviceWorkerRegistration.register();